@import "_normalize";
@import "_variables";
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap&subset=latin-ext');

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  display: flex;
  color: $yellow;
  background: $grey;
  font-family: 'Open Sans', sans-serif;
}

.editor {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 100vh;
  max-width: 800px;
  padding: 0 16px;
  margin: 0 auto;

  &__header {
    margin: 16px auto;
    font-weight: 600;
  }

  &__status {
    width: 100%;
    margin: 0 0 16px 0;
    font-size: 1rem;
    text-align: center;

    @media (min-width: 568px) and (orientation: landscape) {
      font-size: 1.2rem;
    }
  }

  &__text {
    width: 100%;
    border-radius: 10px;
    padding: 8px;
    margin: 8px;

    &:focus {
      outline: none;
      box-shadow: 0px 0px 4px rgb(255, 196, 0);
    }

    @media (min-width: 568px) and (orientation: landscape) {
      height: 40vh;
    }
  }

  &__button {
    width: 100%;
    height: 48px;
    margin: 8px;
    background-color: #222;
    border: none;
    border-radius: 10px;
    text-decoration: none;
    color: white;

    &:hover {
      background-color: #2CD892;
      color: #fff;
    }

    &:focus {
      outline: none;
      color: #fff;
      box-shadow: 0px 0px 4px rgb(255, 196, 0);
    }

    &--filter {
      &:hover {
        background-color: $red;
      }

      -webkit-filter: url("#filter");
      filter: url("#filter");
    }

    @media (min-width: 568px) and (orientation: landscape) {
      width: 29%;
    }

    @media (min-width: 640px) and (orientation: landscape) {
      width: 30%;
    }

    @media (min-width: 812px) and (orientation: landscape) {
      width: 31%;
    }
  }

  @media (min-width: 320px) and (orientation: landscape) {
    flex-flow: row wrap;
    justify-content: space-between;
    margin: 0 30px;
  }

  @media (min-width: 640px) and (orientation: landscape) {
    margin: 0 30px;
  }

  @media (min-width: 640px) and (orientation: landscape) {
    margin: 0 auto;
  }
}

.svg-filters {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 1px;
}