html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  display: flex;
  color: #F9C802;
  background: #2A5966;
  font-family: 'Open Sans', sans-serif; }

.editor {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  height: 100vh;
  max-width: 800px;
  padding: 0 16px;
  margin: 0 auto; }
  .editor__header {
    margin: 16px auto;
    font-weight: 600; }
  .editor__status {
    width: 100%;
    margin: 0 0 16px 0;
    font-size: 1rem;
    text-align: center; }
    @media (min-width: 568px) and (orientation: landscape) {
      .editor__status {
        font-size: 1.2rem; } }
  .editor__text {
    width: 100%;
    border-radius: 10px;
    padding: 8px;
    margin: 8px; }
    .editor__text:focus {
      outline: none;
      box-shadow: 0px 0px 4px #ffc400; }
    @media (min-width: 568px) and (orientation: landscape) {
      .editor__text {
        height: 40vh; } }
  .editor__button {
    width: 100%;
    height: 48px;
    margin: 8px;
    background-color: #222;
    border: none;
    border-radius: 10px;
    text-decoration: none;
    color: white; }
    .editor__button:hover {
      background-color: #2CD892;
      color: #fff; }
    .editor__button:focus {
      outline: none;
      color: #fff;
      box-shadow: 0px 0px 4px #ffc400; }
    .editor__button--filter {
      -webkit-filter: url("#filter");
      filter: url("#filter"); }
      .editor__button--filter:hover {
        background-color: #D13E36; }
    @media (min-width: 568px) and (orientation: landscape) {
      .editor__button {
        width: 29%; } }
    @media (min-width: 640px) and (orientation: landscape) {
      .editor__button {
        width: 30%; } }
    @media (min-width: 812px) and (orientation: landscape) {
      .editor__button {
        width: 31%; } }
  @media (min-width: 320px) and (orientation: landscape) {
    .editor {
      flex-flow: row wrap;
      justify-content: space-between;
      margin: 0 30px; } }
  @media (min-width: 640px) and (orientation: landscape) {
    .editor {
      margin: 0 30px; } }
  @media (min-width: 640px) and (orientation: landscape) {
    .editor {
      margin: 0 auto; } }

.svg-filters {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 1px; }
